.skillpage-comment {
    margin: 30px;
    margin-left: 60px;
    font-family: "メイリオ", sans-serif;
    font-size: 20px;
}

.langbox {
    position: relative;
    background-color: #eeeeee;
    border-radius: 30px;
    height: 110px;
    padding: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top:10px;
    margin-left: 70px;
    margin-right: 70px;
    margin-bottom:10px;
    /* border: solid #333333 1px; */
    color: black;
    overflow: auto;
}

.lang-icon {
    height: 100px;
    width: 100px;
    padding: 5px;
    background-color: transparent;
    /* border: solid #444444 1px; */
    border-radius: 10px;
}

.lang-name { 
    position: absolute;
    padding-top: 20px;
    padding-left: 20px;
    font-size: 50px;
    font-family: "Century Gothic", Helvetica, sans-serif;
}

.langbox-comment {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin-left: 350px;
    font-size: 18px;
    font-family: "メイリオ";
    overflow: auto; 
}
