body {
    background-color: #555555;
    margin:0;
}

div.header {
    z-index: 100;
    height: 130px;
    margin-bottom: 130px;
    width: 100%;
    position: fixed;
    background-color: #333333;
}

div.logo {
    font-family: "Century Gothic", cursive,"メイリオ", Arial Black;
    font-size: 38px;
    color: white;
    padding: 30px;
    padding-top: 17px;
    position: absolute;
    top: 33%;
    transform: translateY(-33%);
}

div.menubar {
    padding-left: 18px;
    background-color: transparent;
    position: absolute;
    top: 100%;
    transform: translateY(-100%);
}

div.menubar > button.SelectedButton {
    font: 30px "Century Gothic", Futura, sans-serif;
    background-color: transparent;
    color: white;
    border-color: transparent;
    border-bottom-color: red;
    border-bottom: 1;
}

div.menubar > button.NotSelectedButton{
    font: 30px "Century Gothic", Futura, sans-serif;
    background-color: transparent;
    color: white;
    border-color: transparent;
    border-bottom-color: blue;
    border-bottom: 1;
}

div.content {
    background-color: transparent;
    color: white;
    padding-top: 130px;
}
